.App {
    height: 100vh;
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: linear-gradient(#000000, #808080);
}
.App h1{
    font-size:70px;
    color:#ffffff;
    animation: title 3s;
}
@keyframes title{
    from{
        transform: rotateY(89deg);
    }
    to{
        transform:rotateY(0deg);
    }

}

.containetInput {
    background-color: #808080;
    padding: 15px;
    margin: 34px 0;
    display: flex;
    border-radius: 8px;
    box-shadow: 1px 3px 8px rgb(0,0,0, 0.5);
    animation: contaniner 5s;
}
.containetInput input{
    background-color:transparent;
    border:0;
    font-size:20px;
    color:#ffffff;
    margin-right:8px;
    outline:none;
    cursor: help;
}

.containetInput input::placeholder{
    color:#f1f1f1;
}
.buttonSearch{
    background-color:transparent;
    border:0;
    display:flex;
    justify-content:center;
    align-items:center;
    cursor: pointer;
}
.buttonSearch:hover {
    transform: scale(1.3);
}

@keyframes contaniner {
    from {
        transform: rotateY(90deg);
        }
    to {
        transform: rotateY(0deg);
        }
}

.main {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: #f1f1f1;
    width:500px;
    border-radius: 8px;
}
.main h2{
    margin:17px;
    font-size:30px;
}
.main span{
    margin-bottom:13px;
    font-weight:bold; 
}
/*650 altera!*/
@media (max-width: 650px){
    .App h1{
        font-size: 55px;
    }
    .containetInput{
        padding:10px;
    }
    .main {
        width: 95%;
        font-size: 18px;
        min-height: 250px;
    }
    .main h2 {
            font-size: 28px;
    }
}